import Promise from 'bluebird';
import bus from '../../bus';
import BaseDriver from '../base';
import { format, log, DOM } from '../../../utils';

const { extractElement } = DOM;
const { elemToFloat, toFloat } = format;

export default class Driver extends BaseDriver {
	constructor(merchantDomain) {
		super();
		this.merchantDomain = merchantDomain;
		this.TOTAL_SELECTOR = '.est-total strong[ng-bind-html^="itemsSubtotal"]';
		this.EMPTY_CART_SELECTOR = '.empty-cart';
		this.PROMO_INPUT_SELECTOR = '#focusPromoInput';
		this.PROTOCOL = window.location.protocol;
		this.BASE_URL = `${this.PROTOCOL}//www.${this.merchantDomain}/`;
		this.BASE_API_URL = `${this.BASE_URL}crsp/api/cart/v1`;

		this.IS_GET_MERCHANT_CODES_METHOD = false;
		this.GET_CODE_REQUEST_URL = this.BASE_URL;
		this.MERCHANT_CODES_ELEMENT_REGEX = '.bootstraprow img';
		this.MERCHANT_CODE_REGEX = /code[:]{0,1}\s(\w+)/i;
	}

	checkCondition() {
		const correctURL = window.location.href.endsWith('crsp/mx/cart#/cart');
		const emptyCart = document.querySelector(this.EMPTY_CART_SELECTOR);
		const codeInput = document.querySelector(this.PROMO_INPUT_SELECTOR);
		const showPopup = !emptyCart && correctURL && codeInput;
		return showPopup;
	}

	getMerchantCodes() {
		if (this.IS_GET_MERCHANT_CODES_METHOD) {
			return fetch(this.GET_CODE_REQUEST_URL, {
				method: 'GET',
			})
				.then(response => response.text())
				.then(responseHTML => {
					return [
						...new Set(
							[...extractElement(this.MERCHANT_CODES_ELEMENT_REGEX, responseHTML, true)]
								.filter(
									item =>
										item.getAttribute('title') &&
										item.getAttribute('title').match(this.MERCHANT_CODE_REGEX),
								)
								.map(item => item.getAttribute('title').match(this.MERCHANT_CODE_REGEX)[1]),
						),
					];
				});
		}
		return [];
	}

	getExistingCodes() {
		return this._getCartSummary().then(responseJSON => {
			const response = responseJSON.response.ordSummary.couponSavings;
			const codes = response ? Object.values(response).map(elem => elem.promoTitle) : [];

			return {
				codes,
				codeCount: codes.length,
			};
		});
	}

	removeCodes(codes) {
		return this._removeCode(codes[0])
			.then(() => this._getCartSummary())
			.then(responseJSON => this._parseTotal(responseJSON));
	}

	checkCodes(codes, originalPrice = this._parseTotal()) {
		log(`Original price: ${originalPrice}`);
		return Promise.each(codes, code => {
			const result = {
				code,
				finalPrice: originalPrice,
				finalDiscount: 0,
			};
			return this._applyCode(code)
				.then(() => this._getCartSummary())
				.then(responseJSON => {
					result.finalPrice = this._parseTotal(responseJSON);
					result.finalDiscount = originalPrice - result.finalPrice;
					return this._removeCode(code).then(() => bus.$emit('code-checked', result));
				});
		});
	}

	applyCodes(codes) {
		return this._applyCode(codes[0]);
	}

	getPreTaxShippingTotal() {
		// on the cart page we have price without tax and shipping
		return this._getCartSummary().then(responseJSON => this._parseTotal(responseJSON));
	}

	_applyCode(code) {
		return this._manageCode(code, 'add');
	}

	_removeCode(code) {
		return this._manageCode(code, 'remove');
	}

	_manageCode(code, endpoint) {
		return fetch(`${this.BASE_API_URL}/coupon/${endpoint}`, {
			method: 'POST',
			credentials: 'include',
			body: JSON.stringify({
				couponRequest: {
					couponCode: code,
				},
			}),
			headers: {
				'Content-Type': 'application/json',
				'X-Requested-With': 'XMLHttpRequest',
			},
		}).then(response => response.json());
	}

	_getCartSummary() {
		return fetch(`${this.BASE_API_URL}/summary/view`, {
			method: 'GET',
			credentials: 'include',
		}).then(response => response.json());
	}

	_parseTotal(objJSON) {
		if (objJSON) {
			return toFloat(objJSON.response.ordSummary.merchTotal);
		}
		return elemToFloat(document.querySelector(this.TOTAL_SELECTOR));
	}
}
