import DriverTemplate from './_base';
import { DOM, browserUtils, format } from '../../../utils';
import { backgroundMessaging } from '../../lib';

const { getCookie, setCookie, wait } = browserUtils;
const { elemToFloat } = format;
const { extractElement } = DOM;

/**
Driver behavior description:
+ Existing code: yes
+ Remove code: yes
+ Stackable: no
+ Free gift support: no
+ Free shipping: no
+ ‘isValid’ implemented: no
+ Codes can be replaced by another code: no
+ Pages, where extension works: cart
+ Is driver SPA: yes
+ Gather codes on site pages: no
+ Special case about the driver works.
   - maxCoupons: 3 (driver counts codes)
   - code testing allow 3 times per 20 min (due to blocking)
 */

const Driver = class Driver extends DriverTemplate {
	constructor() {
		super('kmart.com');
		this.BASE_URL = 'https://www.kmart.com/';
		this.IS_GET_MERCHANT_CODES_METHOD = true;
		this.MERCHANT_CODE_SELECTOR = '.multipleImageHero .obj5 a';
		this.MERCHANT_CODE_MAIN_PAGE_SELECTOR = '.bx-click-to-copy';
		this.MERCHANT_CODE_PROMO_PAGE_SELECTOR = '.offer .title-3';
		this.PROMO_CODE_SELECTOR = '[data-title="ALL OFFER EXCLUSIONS"] b';
		this.MERCHANT_CODE_REGEXP = /code:? (\w+)/i;
		this.PRICES_SELECTOR = '.sci-product-info tr';
		this.PRICE_SELECTOR = '.new-cart-item-sell-price span';
		this.DISABLE_TIME_MS = 20 * 60 * 1000; // 20 minutes
	}

	async checkCondition() {
		const merchantCode = [
			...new Set([...document.querySelectorAll(this.MERCHANT_CODE_MAIN_PAGE_SELECTOR)].map(el => el.textContent)),
		];
		if (this.MERCHANT_CODE_MAIN_PAGE_SELECTOR && !getCookie('MerchantCode')) {
			setCookie('MerchantCode', merchantCode, 60, 'kmart.com');
		}

		let isNotMaxCodeTesting = true;
		const driverStorage = await backgroundMessaging.getDriverStorage(this.BASE_URL);
		if (driverStorage) {
			const dataExpired = Date.now() - driverStorage.lastTestTimestamp >= this.DISABLE_TIME_MS;
			if (dataExpired) {
				backgroundMessaging.deleteDriverStorage(this.BASE_URL);
			} else {
				isNotMaxCodeTesting = driverStorage.codeTestingCount < 3;
			}
		}

		const correctURL = window.location.href.endsWith('crsp/mx/cart#/cart');
		const emptyCart = document.querySelector(this.EMPTY_CART_SELECTOR);
		const codeInput = document.querySelector(this.PROMO_INPUT_SELECTOR);

		return !emptyCart && correctURL && codeInput && isNotMaxCodeTesting;
	}

	async getMerchantCodes() {
		const merchantCodes = [];

		merchantCodes.push(getCookie('MerchantCode'));

		const mainPage = await this._getPage(this.BASE_URL);
		merchantCodes.push(
			...[...extractElement(this.MERCHANT_CODE_SELECTOR, mainPage, true)]
				.map(item => item.getAttribute('onclick').match(this.MERCHANT_CODE_REGEXP))
				.filter(Boolean)
				.map(elem => elem[1]),
		);
		merchantCodes.push(
			...[...extractElement(this.PROMO_CODE_SELECTOR, mainPage, true)]
				.map(item => item.textContent.match(this.MERCHANT_CODE_REGEXP))
				.filter(Boolean)
				.map(elem => elem[1]),
		);

		const iframeElem = document.createElement('iframe');
		iframeElem.src = `${this.BASE_URL}en_us/savings/coupons.html/couponGrid`;
		Object.assign(iframeElem.style, { display: 'none' });
		document.documentElement.appendChild(iframeElem);
		const getCodeElem = () => {
			if (iframeElem && iframeElem.contentDocument) {
				return [
					...new Set(
						[...iframeElem.contentDocument.querySelectorAll(this.MERCHANT_CODE_PROMO_PAGE_SELECTOR)].filter(
							item => item.textContent.match(this.MERCHANT_CODE_REGEXP),
						),
					),
				].length;
			}
			return null;
		};
		await wait(getCodeElem, 30000, 300, true);

		if (getCodeElem()) {
			merchantCodes.push(
				...[...iframeElem.contentDocument.querySelectorAll(this.MERCHANT_CODE_PROMO_PAGE_SELECTOR)]
					.filter(item => item.textContent.match(this.MERCHANT_CODE_REGEXP))
					.map(item => item.textContent.match(this.MERCHANT_CODE_REGEXP)[1]),
			);
		}

		return [...new Set(merchantCodes.filter(Boolean))];
	}

	async beforeApplyCodes() {
		const driverStorage = await backgroundMessaging.getDriverStorage(this.BASE_URL);
		const codeTestingCount = driverStorage ? driverStorage.codeTestingCount : 0;
		backgroundMessaging.setDriverStorage(this.BASE_URL, {
			codeTestingCount: codeTestingCount + 1,
			lastTestTimestamp: Date.now(),
		});
	}

	getPreTaxShippingTotal() {
		const donationEl = this._findElement('Donation');
		const donation = donationEl ? elemToFloat(donationEl) || 0 : 0;
		// on the cart page we have price without tax and shipping
		return this._getCartSummary().then(responseJSON => this._parseTotal(responseJSON) - donation);
	}

	_findElement(targetString) {
		const targetEl = [...document.querySelectorAll(this.PRICES_SELECTOR)].find(el =>
			el.textContent.includes(targetString),
		);
		return targetEl ? targetEl.querySelector(this.PRICE_SELECTOR) : null;
	}

	_getPage(url) {
		return fetch(url, {
			method: 'GET',
			credentials: 'include',
		}).then(response => response.text());
	}
};

export default {
	name: 'Kmart',
	domain: 'kmart.com',
	Driver,
	isSinglePageApp: true,
	maxCoupons: 3,
};
