import DriverTemplate from './_base';
import { backgroundMessaging } from '../../lib';
import { format, browserUtils } from '../../../utils';
import { extractElement } from '../../../utils/DOM';

const { elemToFloat } = format;
const { wait } = browserUtils;

/*
Driver behavior description:
+ Existing code: yes
+ Remove code: yes
+ Stackable: no
+ Free gift support: no
+ Free shipping: no
+ ‘isValid’ implemented: no
+ Codes can be replaced by another code: no
+ Pages, where extension works: cart
+ Is driver SPA: yes
+ Gather codes on site pages: yes, from coupon pages
	('https://www.sears.com/en_us/savings/coupons.html/couponGrid',
	'https://www.sears.com/deals/center/hot-deals.html/dealGrid'):
  sometimes page loading take a lot of time, we wait to get coupon 20 sec
+ MaxCoupons: 3 (driver counts codes)
+ Can run codes testing 2 times then need to wait 60 min to the next codes testing
 */

const DOMAIN = 'sears.com';

const Driver = class Driver extends DriverTemplate {
	constructor() {
		super(DOMAIN);
		this.IS_GET_MERCHANT_CODES_METHOD = true;
		this.GET_CODE_REQUEST_URL = `${this.BASE_URL}home`;
		this.DISABLE_TIME_MS = 60 * 60 * 1000; // 60 minutes
		this.ITEM_CONTAINER_SELECTOR = '[timetrack="Cart Items"]';
		this.DONATION_SELECTOR = '.item-desc[title*="Donation"]';
		this.PRICE_SELECTOR = '.new-cart-item-sell-price';

		this.MERCHANT_CODE_REGEX = /"cso":"(\w+\d+)/i;
		this.MERCHANT_HOT_DEALS_REGEX = /code: (\w+)/i;
		this.MERCHANT_CODE_SELECTOR = '[coupon], .api-card.card.deals-card li[message-deals] .ng-binding';
		this.MERCHANT_CODE_SELECTOR_CART = '.bx-click-to-copy';

		this.URL_ID_REGEX = /dealsComponent\/clientlibs.min.([a-zA-Z0-9_=-]+)/;
		this.APP_ID_REGEX = /appID=(\w+)/;
		this.AUTH_ID_REGEX = /authID=(\w+)/;
	}

	async checkCondition() {
		let isNotMaxCodeTesting = true;
		const driverStorage = await backgroundMessaging.getDriverStorage(DOMAIN);
		if (driverStorage) {
			const dataExpired = Date.now() - driverStorage.lastTestTimestamp >= this.DISABLE_TIME_MS;
			if (dataExpired) {
				backgroundMessaging.deleteDriverStorage(DOMAIN);
			} else {
				isNotMaxCodeTesting = driverStorage.codeTestingCount < 2;
			}
		}
		return super.checkCondition() && isNotMaxCodeTesting;
	}

	async getMerchantCodes() {
		const iframeElemDealGrid = document.createElement('iframe');
		iframeElemDealGrid.src = `https://www.${DOMAIN}/deals/center/hot-deals.html/dealGrid/`;
		Object.assign(iframeElemDealGrid.style, { display: 'none' });
		document.documentElement.appendChild(iframeElemDealGrid);
		const isFrameLoaded = () => iframeElemDealGrid.contentDocument.querySelector(this.MERCHANT_CODE_SELECTOR);
		await wait(isFrameLoaded, 20000, 300, true);
		const dealGridPageCodes = [...iframeElemDealGrid.contentDocument.querySelectorAll(this.MERCHANT_CODE_SELECTOR)]
			.map(el => el.textContent.match(this.MERCHANT_HOT_DEALS_REGEX))
			.filter(Boolean)
			.map(el => el[1]);

		const iframeElemСouponGrid = document.createElement('iframe');
		iframeElemСouponGrid.src = `https://www.${DOMAIN}/en_us/savings/coupons.html/couponGrid`;
		Object.assign(iframeElemСouponGrid.style, { display: 'none' });
		document.documentElement.appendChild(iframeElemСouponGrid);
		// to get coupon code need offer code
		const offerCodeInfo = () =>
			iframeElemСouponGrid.contentDocument.querySelector(this.MERCHANT_CODE_SELECTOR) &&
			iframeElemСouponGrid.contentDocument.querySelector(this.MERCHANT_CODE_SELECTOR).getAttribute('coupon');
		// page loading take a long time
		await wait(offerCodeInfo, 20000, 300, true);

		let couponGridPageCodes = [];

		if (offerCodeInfo()) {
			const couponDetail = JSON.parse(offerCodeInfo());
			const urlID = this._getID(iframeElemСouponGrid.contentDocument, this.URL_ID_REGEX);
			const { offerCode, offerSource } = couponDetail;
			const merchantCodeResponse = await this._getCouponCode(offerCode, offerSource, urlID);
			const merchantCode = merchantCodeResponse.match(this.MERCHANT_CODE_REGEX);
			couponGridPageCodes = [merchantCode && merchantCode[1]].filter(Boolean);
		}

		const codesFromPage = extractElement(this.MERCHANT_CODE_SELECTOR_CART, null, true);

		return [...new Set([...couponGridPageCodes, ...dealGridPageCodes, ...codesFromPage])].filter(
			code => typeof code === 'string' && Boolean(code),
		);
	}

	getExistingCodes() {
		return this._getCartSummary().then(responseJSON => {
			const response = responseJSON.response.ordSummary.couponSavings;
			const codes = response
				? Object.values(response)
						.map(elem => elem.promoTitle)
						.filter(code => typeof code === 'string' && Boolean(code))
				: [];

			return {
				codes,
				codeCount: codes.length,
			};
		});
	}

	async beforeApplyCodes() {
		const driverStorage = await backgroundMessaging.getDriverStorage(DOMAIN);
		const codeTestingCount = driverStorage ? driverStorage.codeTestingCount : 0;
		backgroundMessaging.setDriverStorage(DOMAIN, {
			codeTestingCount: codeTestingCount + 1,
			lastTestTimestamp: Date.now(),
		});
	}

	getPreTaxShippingTotal() {
		// on the cart page we have price without tax and shipping, only with donation
		return this._getCartSummary().then(responseJSON => {
			const donation = [...document.querySelectorAll(this.ITEM_CONTAINER_SELECTOR)]
				.filter(itemEl => itemEl.querySelector(this.DONATION_SELECTOR))
				.map(donationEl => elemToFloat(donationEl.querySelector(this.PRICE_SELECTOR)))
				.reduce((cur, prev) => cur + prev, 0);
			return this._parseTotal(responseJSON) - donation;
		});
	}

	_getID(response, regex) {
		return [...response.querySelectorAll('script')]
			.filter(el => el.src.match(regex))
			.map(el => el.src.match(regex)[1])[0];
	}

	_getIDResponse(urlID) {
		return fetch(`https://www.${DOMAIN}/etc/designs/shc/dealsComponent/clientlibs.min.${urlID}.js`, {
			method: 'GET',
			credentials: 'include',
		}).then(response => response.text());
	}

	_parseIDs(response) {
		const authID = response.match(this.AUTH_ID_REGEX)[1];
		const appID = response.match(this.APP_ID_REGEX)[1];
		return { authID, appID };
	}

	_getCouponCode(offerCode, offerSource, urlID) {
		return this._getIDResponse(urlID)
			.then(response => this._parseIDs(response))
			.then(({ authID, appID }) =>
				fetch(
					`https://api.${DOMAIN}/shcapi/v3/ecoupon/jsonp/generateCouponDetail?appID=${appID}&authID=${authID}&client=dc&action=generateCSO&offerCode=${offerCode}&offerSource=${offerSource}&channel=sears.com&callback=angular.callbacks._1`,
				).then(response => response.text()),
			);
	}
};

export default {
	name: 'Sears',
	domain: 'sears.com',
	Driver,
	isSinglePageApp: true,
	maxCoupons: 3,
};
